.card-container {
    width: 100%;
    border-radius: 10px;
    text-align: center;
    overflow: hidden;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: rgb(255, 255, 255);
  }
  
  .card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    color: black;
    font-weight:900;
    font-size: 16px;
  }
  
  .question-mark {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: rgb(233, 231, 231);
    color: rgb(175, 175, 175);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    font-weight: bold;
  }
  
  .card-content {
    padding: 10px 0;
    font-size: 24px;
    font-weight: bold;
    color: #000;
    font-family: 'Roboto', sans-serif;
    background-color: rgb(255, 255, 255);
  }
  